class ContentCore {
  constructor() {
    this._services   = {};
    this._components = {};
  }

  registerService(name, handler) {
    if (typeof handler._setContentCoreObject === 'function') {
      handler._setContentCoreObject(this);
    }
    this._services[name] = handler;
  }

  makeComponent(componentClass) {
    const component = new componentClass();
    component._setContentCoreObject(this);
    component.init();
    return component;
  }

  /**
   * It's a facade for registered services
   * @param name
   * @returns {*}
   */
  getService(name) {
    return this._services[name];
  }

  entryPoint(app) {
    app._setContentCoreObject(this);
    this._app = app;
  }

  run() {
    this._app.run();
  }
}

export default ContentCore;