import BaseComponent   from '../../core/BaseComponent';
import ContentMessages from '../../../messages/ContentMessages';

class CookiesAndCachePopup extends BaseComponent {
  init() {
    this.getService('contentMessageDispatcher')
    .subscribe(ContentMessages.SHOW_POPUP_COOKIES_AND_CACHE_IS_CLEAN, async (message) => {
      await this.onShow(message);
    });
  }

  async onShow(message) {
    if (document.querySelector('#cook_popup')) {
      return;
    }

    let popup = document.createElement('div');
    popup.setAttribute('style', "background: url('" + chrome.runtime.getURL('images/cookies_ok.png') + "') no-repeat top left; position: fixed; top: 40% ; left: 40%;  width: 100%; height: 100%; z-index: 9999999 ");
    popup.setAttribute('id', 'cook_popup');
    document.body.appendChild(popup);
    document.getElementById('cook_popup').addEventListener('click', function () {
      let div = document.getElementById('cook_popup');
      div.parentNode.removeChild(div);
    });
  }
}

export default CookiesAndCachePopup;