class ContentMessageDispatcher {
  constructor() {
    this._messages = {};

    chrome.runtime.onMessage.addListener((message, sender, response) => {
      if (typeof this._messages[message.method] !== 'undefined') {
        this._messages[message.method](message, sender, response).then();
      }
      return false;
    });
  }

  subscribe(messageName, handler) {
    this._messages[messageName] = handler;
  }

  async sendMessageToBackgroundPage(message) {
    return new Promise((resolve, reject) => {
      try {
        chrome.runtime.sendMessage(message, (response) => {
          resolve(response);
        });
      }
      catch (e) {
        reject();
      }
    });
  }
}

export default ContentMessageDispatcher;